<script setup>
  //자바스크립트 코드 작성 구간
  import * as echarts from 'echarts'
  import {ref, onMounted, nextTick, onBeforeUnmount} from 'vue'
  import Papa from 'papaparse'

  const chartRef = ref(null)
  let myChart = null
  //차트 옵션
  let option;

  //일,주,월간 이용자 수
  let dailyaac = ref(0);
  let weeklyaac = ref(0);
  let monthlyaac = ref(0);

  //Datazoom 최소, 최댓값 반응형
  let minPrice = ref(0);
  let maxPrice = ref(0);
  let minAccounts = ref(0);
  let maxAccounts = ref(0);

  //정규화 함수
  function normalize(val, min, max){
    if (min==max) return 0
    const n = (val-min)/(max-min)
    return n;
  }

  // 일간, 주간, 월간(미구현) 모드 변경 (보류)
  // //현재 탐색 모드
