  //     }
  //     chartData = chunks;

  //     console.log(chunks);
  //   }

  //   updateChart();
  // }

  function updateChart(){
        option = {
          animaiton: false,
          graphic: {
            type: 'group',
            left: '1%',  // 전체 그룹을 가운데 정렬
            top: '3%',       // 위쪽 여백
            children: [
              {
                type: 'image',
                id: 'logo',
                style: {
                  image: '/assets/hlogo.png',
                  width: 40,
                  height: 40,
                },
                left: 0,
                top: 0
              },
              {
                type: 'text',
                id: 'title',
                left: 45, // 이미지 오른쪽으로 약간 띄움
                top: 3,  // 높이 맞추기 (이미지 높이에 따라 조정)
                style: {
                  text: 'COSMOS',
                  fill: '#000',
                  font: 'bold 40px Pretendard',
                  textAlign: 'left',
                  textVerticalAlign: 'middle'
                }
              }
            ]
          },
          grid:[
            {
              left:'5%',
              right:'5%',
              top:'15%',
              height:'45%',
              zlevel:-2
            },
            {
              show:false,
              borderColor:'rgba(200,200,200,1)',
              left:'5%',
              right:'5%',
              top:'65%',
              height:'20%',
              zlevel:-2
            }
          ],
          tooltip: {
            trigger: 'axis',
            axisPointer:{
              type:'cross'
            },
            backgroundColor: '#FFFFFF',
            textStyle:{
              color:'black'
            },
            borderColor:'rgba(180,180,180,1)',
            formatter: function (params){
              const idx = params[0].dataIndex;
              let date = chartData[idx].timestamp;
              date = date.slice(0,-1)
              let t = `<span style="font-size:16px">${date}</span>`;
              let t2 = ''
              let diff = null
              let curPrice = null
              let prevPrice = null
              if(idx > 0){
                prevPrice = chartData[idx-1].marketPrice;
              }
              else{
                prevPrice = 0
              }

              for(const p of params){
                if(p.seriesName == 'ActiveAccount'){
                  t2 += `${p.seriesName}: <span style="font-weight:bold">${p.value.toFixed(2)}K</span><br>`;
                }
                else if(p.seriesName == 'MarketPrice'){
                  t2 += `${p.seriesName}: <span style="font-weight:bold">${p.value.toFixed(2)}</span><br>`;
                  curPrice = p.value
                }
                else if(p.seriesName == 'MarketPrice(Normalized)'){
                  t2 += `${p.seriesName}: <span style="font-weight:bold">${p.value.toFixed(2)}</span><br>`;
                }
                else if(p.seriesName == 'ActiveAccount(Normalized)'){
                  t2 += `${p.seriesName}: <span style="font-weight:bold">${p.value.toFixed(2)}</span><br>`;
                }
              }

              diff = curPrice - prevPrice;
              if(diff > 0){
                t += `<span style="color:#4E9D70; float:right;"> &#9650;$${diff.toFixed(2)}</span><br>`
              }
              else if(diff < 0){
                t += `<span style="color:#BF5355; float:right;">	&#9660;$${Math.abs(diff).toFixed(2)}</span><br>`
              }
              else{
                t += `0`
              }

              t += t2;
              return t;
            } 
          },
          axisPointer: {
            link: { xAxisIndex: 'all' },
            label: {
              backgroundColor: 'rgba(80, 80, 80, 1)'
            }
          },
          legend:{
            data:['MarketPrice', 'ActiveAccount', 'MarketPrice(Normalized)', 'ActiveAccount(Normalized)'],
            left:'center',
            textStyle:{
              color:'black'
            },
            top:'90%',
            itemGap:50
          },
          xAxis: [
            {
              type: 'category',
              data:chartData.map(d=>d.timestamp.slice(6, -1)),
              axisLabel: {
                color:'gray'
              },
              splitLine:{
                show:false
              },
              axisTick:{
                show:false
              },
              axisLine:{
                show:true
              },
              boundaryGap : false,
              zlevel:-2
            },
            {
              gridIndex:1,
              data:chartData.map(d=>d.timestamp.slice(6,-1)),
              boundaryGap: false,
              axisLine: { onZero: false },
              axisTick: { show: false },
              splitLine: { show: false },
              axisLabel: { 
                show: false
              },
            }
          ],
          yAxis: [
            {
              type:'value',
              position:'left',
              axisLabel: {
                color:'gray'
              },
              splitNumber:5,
              splitLine:{
                lineStyle:{
                  color:'rgba(235, 235, 235,1)'
                }
              },
              min:function(val){
                return val.min;
              },
              zlevel:-2
            },
            {
              gridIndex:1,
              axisLabel: { show: true },
              axisLine: { show: false },
              axisTick: { show: false },
              splitLine: { show: false },
              splitNumber: 1
            }
          ],
          dataZoom:
          {
            type:'inside',
            xAxisIndex:[0,1],
            start:0,
            end:100
          },
          series: [
            {
              type: 'line',
              animation:false,
              name: 'MarketPrice',
              data: chartData.map(d=>d.marketPrice),
              smooth:false,
              lineStyle:{
                color:'#F17C6F'
              },
              itemStyle:{
                color:'#F17C6F'
              },
              showSymbol:false
            },
            {
              type: 'line',
              animation:false,
              name: 'ActiveAccount',
              data: chartData.map(d=>d.activeAccountCount),
              smooth:false,
              lineStyle:{
                color:'#7DCFA0'
              },
              itemStyle:{
                color:'#7DCFA0'
              },
              showSymbol:false
            },
            {
              type:'line',
              name:'MarketPrice(Normalized)',
              animaiton:false,
              data: chartData.map(d=>normalize(parseFloat(d.marketPrice), minPrice.value, maxPrice.value)),
              yAxisIndex: 1,
              xAxisIndex: 1,
              lineStyle:{
                color:'#2D83D9'
              },
              itemStyle:{
                color:'#2D83D9'
              },
              showSymbol:false
            },
            {
              type:'line',
              name:'ActiveAccount(Normalized)',
              animaiton:false,
              data: chartData.map(d=>normalize(parseFloat(d.activeAccountCount), minAccounts.value, maxAccounts.value)),
              yAxisIndex: 1,
              xAxisIndex: 1,
              lineStyle:{
                color:'#FDDE61'
              },
              itemStyle:{
                color:'#FDDE61'
              },
              showSymbol:false
            }
          ]
        }

        option && myChart.setOption(option)
  }

  let data = [];
  let chartData = [];

  onMounted(async() => {
    await nextTick()
    
    //데이터 파일 읽기
    const response = await fetch('/data/cosmos.csv')
    const csvText = await response.text()
    //데이터 파싱
    const parsed = Papa.parse(csvText, {header: true, skipEmptyLines: true})
    data = parsed.data
    //데이터 순서 반전
    data = data.reverse()

    chartData = data.map(d => ({
      timestamp: new Date(Number(d.timestamp)).toLocaleDateString(),
      marketPrice: parseFloat(d.marketPrice),
      activeAccountCount: parseFloat(d.activeAccountCount/1000)
    }))
    
    //일,주,월간 이용자 확인
    dailyaac.value = (chartData.at(-1).activeAccountCount*1000).toLocaleString();
    
    const weeksliced = chartData.slice(-7);
    weeklyaac.value = weeksliced.reduce((a,c ) => a + c.activeAccountCount*1000, 0).toLocaleString()

    const monsliced = chartData.slice(-30);
    monthlyaac.value = monsliced.reduce((a, c) => a + c.activeAccountCount*1000, 0).toLocaleString();

    minPrice.value = Math.min(...chartData.map(d => d.marketPrice))
    maxPrice.value = Math.max(...chartData.map(d => d.marketPrice))
    minAccounts.value = Math.min(...chartData.map(d => d.activeAccountCount))
    maxAccounts.value = Math.max(...chartData.map(d => d.activeAccountCount))

    myChart = echarts.init(chartRef.value)

    myChart.on('dataZoom', (params) => {
      const zoom = params.batch ? params.batch[0] : params
      const start = zoom.start ?? 0
      const end = zoom.end ?? 100
      const total = chartData.length
      const startIdx = Math.floor((start / 100) * total)
      const endIdx = Math.ceil((end / 100) * total)

      const visibleData = chartData.slice(startIdx, endIdx)
      minPrice.value = Math.min(...visibleData.map(d => d.marketPrice))
      maxPrice.value = Math.max(...visibleData.map(d => d.marketPrice))
      
      minAccounts.value = Math.min(...visibleData.map(d => d.activeAccountCount)),
      maxAccounts.value = Math.max(...visibleData.map(d => d.activeAccountCount))
      
      myChart.setOption({
        series: [
          {
            name: 'MarketPrice(Normalized)',
            data: chartData.map(d => normalize(d.marketPrice, minPrice.value, maxPrice.value))
          },
          {
            name: 'ActiveAccount(Normalized)',
            data: chartData.map(d => normalize(d.activeAccountCount, minAccounts.value, maxAccounts.value))
          }
        ]
      })

      console.log(minPrice.value, maxPrice.value, minAccounts.value, maxAccounts.value)
    })

    window.addEventListener('resize', () => {
      if (myChart) myChart.resize()
    })

    updateChart();
    
  })

  onBeforeUnmount(() => {
  // ✅ 인스턴스 정리
  if (myChart) {
    myChart.dispose()
    myChart = null
  }
  window.removeEventListener('resize', () => {
    if (myChart) myChart.resize()
  })
})
      
</script>